  background: #272626;
}

.page-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.page-scroll .page-scroll__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.page-scroll .page-scroll__blackout {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #000;
  pointer-events: none;
}
.page-scroll .page-scroll__item-wrapper {
  min-height: 100%;
  position: relative;
  background: #ffffff;
}

/* Header CSS */
header {
  width: 100%;
  z-index: 3;
}
header .header {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  /*  justify-content: space-between; */
  padding: 50px 0;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  position: relative;
  z-index: 1;
}
header .header .header-logo {
  width: 45px;
}
header .header .header-nav {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 60px;
  margin: 0 auto;
}
header .header .header-nav > a {
  font-size: 0.8rem;
  color: #ffffff;
  overflow: hidden;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  border-bottom: 1px solid transparent;
}
header .header .header-nav > a span {
  position: relative;
  display: inline-block;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
header .header .header-nav > a span:before {
  content: attr(data-hover);
  position: absolute;
  top: 100%;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
header .header .header-nav > a:hover {
  border-color: #FFF;
}
header .header .header-nav > a:hover span {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
header .header .header-contacts {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 60px;
}
header .header .header-contacts .header-contacts_socs {
  gap: 50px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
header .header .header-contacts .header-contacts_soc {
  width: 30px;
  position: relative;
  /*  overflow: hidden; */
}
header .header .header-contacts .header-contacts_soc:before {
  z-index: -1;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  width: 0;
  height: 0;
  border-radius: 50%;
  opacity: 0;
  visibility: hidden;
  background: #F93C00;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
header .header .header-contacts .header-contacts_soc:hover:before {
  visibility: visible;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  opacity: 1;
}
header:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  opacity: 0;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  z-index: -1;
}
header.header-mini:after {
  opacity: 1;
  background: rgba(0, 0, 0, 0.8);
}
header.header-mini .header {
  padding: 10px 0;
}

@-webkit-keyframes animHeader {
  from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes animHeader {
  from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
